import { MessageBox } from 'mint-ui'
import { Button } from 'mint-ui';
  export default {
      name: "mobileLogin",
    data(){
        return{
          api: this.GLOBAL.baseURL,
          step:1,
          number:'',
          send:false,
          telEroor:false,
          input1:"",
          input2:'',
          input3:'',
          input4:'',
          input5:'',
          input6:'',
          mobileCode:'+86 中国',
          isChoose:false,
          contryList:[
            {title:'1'},
          ],
          chooseIndex:-1,
          sms:'',
          timeOut:60,
          changeTime:"",
          clickIndex:0,
          isPassword:false,
          phoneSms:'',
          password:''
        }
    },
    watch:{
      number:function(){
        if(this.number){
          this.send = true
        }else{
          this.send = false
          this.telEroor = false
        }
      }
    },
    components:{
      Button,
      MessageBox
    },
    methods:{
      passLogin(){
        this.$http.post(this.api + '/home/User/login_psw',{
          token: localStorage.getItem('token'),
          m_code:  parseInt(this.mobileCode),
          mobile: this.number,
          password: this.password
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$router.push('/index')
              localStorage.setItem('isLogin',true)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.passLogin()
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
            }
          })
      },
      toPassword(){
        this.isPassword = !this.isPassword
      },
      againSms(){
        this.$http.post(this.api + '/home/User/send_msg',{
          token: localStorage.getItem('token'),
          m_code: parseInt(this.mobileCode) ,
          mobile: this.number,
          flag: 3
        })
          .then(res=>{
            if(res.data.code == 1){
              this.step = 2
              this.changeTime = setInterval(()=>{
                this.timeOut --
                this.sms ='重新发送'+ this.timeOut
                if(this.timeOut == 0){
                  this.sms = ''
                  this.timeOut = 60
                  clearInterval(this.changeTime)
                }
              },1000)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.code)
                  this.againSms()
                })
            }else if(res.data.code == 0){
              MessageBox.alert(res.data.msg)
            }
          })
      },
      goBack(){
        this.$router.go(-1)
      },
      toSend(){
        if(!this.sms){
          if(!(/^1[3456789]\d{9}$/.test(this.number))){
            this.telEroor = true
          }else{
            this.againSms()
          }
        }
      },
      toChoose(){
        /*this.isChoose = !this.isChoose*/
      },

      //验证登陆
      telLogin(){
        this.$http.post(this.api + '/home/User/login_sms',{
          token: localStorage.getItem('token'),
          m_code: parseInt(this.mobileCode),
          sms_code: this.phoneSms,
          mobile: this.number,
        })
          .then(res=>{
            if(res.data.code == 1){
              this.$router.push('/index')
              localStorage.setItem('isLogin',true)
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.telLogin()
                })
            }else if(res.data.code == 0){
              MessageBox.alert(res.data.msg)
            }
          })
      },
      toWeixin(){
        window.open(this.api + '/WechaLoginWap?token='+ localStorage.getItem('token')+'&baseurl='+encodeURIComponent(location.origin))
      }
  },
    mounted(){

    }
  }



